.friendList {
  list-style: none;
  padding: 0;
  width: 300px;
  margin-right: auto;
  margin-left: auto;
}
.item {
  display: flex;
  align-items: center;
  padding: 10px;
  box-shadow: 1px 1px 5px rgba(109, 109, 109, 0.384);
}
.item:not(:last-child) {
  margin-bottom: 20px;
}
.statusIsOnline {
  margin-right: 10px;
  border-radius: 50%;
  border: 2px solid rgba(0, 128, 0, 0.6);
  width: 10px;
  height: 10px;
  text-align: center;
  position: relative;
}
.statusIsOffline {
  margin-right: 10px;
  border-radius: 50%;
  border: 2px solid rgba(255, 0, 0, 0.6);
  width: 10px;
  height: 10px;
  text-align: center;
  position: relative;
}
.avatar {
  margin-right: 10px;
}
.name {
  font-size: 20px;
  font-weight: 500;
}
